import React from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import Logo from '../Logo';

import {
  Container,
  NavBar,
  NavItem,
  NavItemDropdown,
  SearchForm,
  SearchInput,
  SearchButton,
} from '../../styles/components/Header.styles';

const Header: React.FC = () => {
  const metaData = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `);

  return (
    <Container>
      <NavBar bg="dark" variant="dark" expand="lg">
        <NavBar.Brand href="/">
          {<Logo /> || metaData.site.siteMetadata?.title}
        </NavBar.Brand>
        <NavBar.Toggle aria-controls="basic-navbar-nav" />
        <NavBar.Collapse id="basic-navbar-nav">
          <NavItem className="ml-auto">
            <NavItem.Link href="/">Home</NavItem.Link>
            <NavItemDropdown title="Dropdown" id="basic-nav-dropdown">
              {['1', '2', '3'].map(item => (
                <NavItemDropdown.Item key={item} href={`#${item}`}>
                  {item}
                </NavItemDropdown.Item>
              ))}
              <NavItemDropdown.Divider />
              <NavItemDropdown.Item href="/blog/my-first-post">
                Separated link
              </NavItemDropdown.Item>
            </NavItemDropdown>
          </NavItem>
          <SearchForm inline>
            <SearchInput type="text" placeholder="Search" className="mr-sm-2" />
            <SearchButton variant="outline-success">Search</SearchButton>
          </SearchForm>
        </NavBar.Collapse>
      </NavBar>
    </Container>
  );
};

export default Header;
