import React, { ReactChild } from 'react';

import GlobalStyles from '../styles/global';
import SEO from './Seo';

interface TemplateProps {
  siteTitle: string;
  children: ReactChild;
}

const Template: React.FC<TemplateProps> = ({
  siteTitle,
  children,
}: TemplateProps) => {
  const metaData = {
    title: siteTitle,
    description: 'A enciclopdia da comdia.',
    lang: 'pt-BR',
    meta: { name: 'string', content: 'any' },
  };

  return (
    <>
      <SEO {...metaData} />
      {children}
      <GlobalStyles />
    </>
  );
};

export default Template;
